import React from "react";
import ReactDOM from "react-dom";
import App from "./App";
import reportWebVitals from "./reportWebVitals";
import { Provider } from "react-redux";
import { createBrowserRouter, RouterProvider } from "react-router-dom";
import AboutMe from "./components/pages/AboutMe";
import DemoRouter2 from "./components/pages/DemoRouter2";
import { TodoContainer } from "./components/pages/TodoPresenter/feature";
import { store } from "./store/store";

const router = createBrowserRouter([
  {
    path: `${process.env.PUBLIC_URL}/`,
    element: <App />,
  },
  {
    path: `${process.env.PUBLIC_URL}/demo1`,
    element: <AboutMe />,
  },
  {
    path: `${process.env.PUBLIC_URL}/demo2`,
    element: <DemoRouter2 />,
  },
  {
    path: `${process.env.PUBLIC_URL}/todo`,
    element: <TodoContainer />,
  },
]);

ReactDOM.render(
  <React.StrictMode>
    <Provider store={store}>
      <RouterProvider router={router} />
    </Provider>
  </React.StrictMode>,
  document.getElementById("root")
);

reportWebVitals();
